import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './style.scss';

class PaginationComponent extends Component {
    render() {
        let { offset, limit, total, onPrevClick, onNextClick, dropDownValues, onLimitChange, filtered, filteredData } = this.props;
        return (
            <div className="pagination-container">
                <button
                    className="navigation-button pointer"
                    disabled={offset === 0 ? true : false}
                    onClick={() => onPrevClick("prev")}>
                    <i className="fa fa-angle-left" aria-hidden="true"></i>
                </button>
                <div className="pagination-text">{offset} to {offset + limit <= total ? offset + limit : total} of {total}</div>
                <button
                    className="navigation-button pointer"
                    disabled={offset + limit >= total ? true : false}
                    onClick={() => onNextClick("next")}>
                    <i className="fa fa-angle-right" aria-hidden="true"></i>
                </button>
                {Array.isArray(dropDownValues) && dropDownValues.length > 0 && <select className="custom-dropdown" onChange={onLimitChange} value={limit}>
                    {dropDownValues.map((option, index) => <option key={index} value={option}>{option}</option>)}
                </select>}

                {Array.isArray(filtered) &&
                    filtered.length > 0 &&
                    Array.isArray(filteredData) &&
                    filteredData.length > 0 &&
                    <div className="matched-records-data"><b>Matched Records :</b> {filteredData.length}</div>}
            </div>
        );
    }
}

PaginationComponent.defaultProps = {
    offset: 0,
    limit: 0,
    total: 0,
    dropDownValues: []
}

PaginationComponent.propTypes = {
    offset: PropTypes.number.isRequired,
    limit: PropTypes.number.isRequired,
    total: PropTypes.number.isRequired,
    onPrevClick: PropTypes.func.isRequired,
    onNextClick: PropTypes.func.isRequired,
    dropDownValues: PropTypes.array,
    onLimitChange: PropTypes.func
}

export default PaginationComponent;