import React from 'react';
import { getDateTime } from '../../../../config/time';

const Timestamp = (props) => {
    if (props.value) {
        return <div>{getDateTime(props.value)}</div>
    }
    return ""
}

export const timestampFilter = (filter, row) => {
    return row._original[filter.id] && String(getDateTime(String(row._original[filter.id]))).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())
}

export default Timestamp;