import { GET_COUPONS_DETAILS, GET_COUPON_ORDERS_DETAILS, RESET_COUPON_DATA } from "./constants";
import { showLoader, hideLoader } from "../../components/Loader/action";
import handleError from "../../errorHandler";
import { getCouponDetails, getCouponOrderDetails } from "../../api";

export function getCouponDetailsAction(params) {
    return (dispatch) => {
        dispatch(showLoader());
        getCouponDetails(params)
            .then(response => {
                dispatch(hideLoader());
                dispatch(getCouponDetailsActionSuccess(response.data));
                dispatch(getCouponOrderDetailsAction(params));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function getCouponDetailsActionSuccess(data) {
    return {
        type: GET_COUPONS_DETAILS,
        data
    }
}

export function getCouponOrderDetailsAction(params) {
    return (dispatch) => {
        dispatch(showLoader());
        getCouponOrderDetails(params)
            .then(response => {
                dispatch(getCouponOrderDetailsSuccess(response.data));
                dispatch(hideLoader());
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function getCouponOrderDetailsSuccess(data) {
    return {
        type: GET_COUPON_ORDERS_DETAILS,
        data
    }
}

export function resetCouponData() {
    return {
        type: RESET_COUPON_DATA
    }
}