import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PageHeader from '../../components/PageHeader';
import ListCouponOrderDeatailsTable from './components/couponDetailsTable';
import { getCouponDetailsAction, getCouponOrderDetailsAction, resetCouponData } from './action';
import CouponDetails from './components/couponDetails'

class ListCouponOrderDeatails extends Component {

    componentDidMount() {
        let payload = {
            "coupon_uid": this.props.couponUid
        }
        this.props.getCouponDetailsAction(payload);
    }

    componentWillUnmount = () => {
        this.props.resetCouponData()
    }

    getNoOrders = () => <div className="no-orders">There is No orders with this Coupon, Please Try After Some Time...</div>

    render() {
        let { couponsOrder, couponsDetails, userDetails } = this.props;
        console.log(this.props);

        let title = `COUPON BASED ORDERS LIST ${(couponsOrder && couponsOrder.count ? `- (${couponsOrder.count})` : '')}`;
        return (
            <div>
                <PageHeader
                    header={title}
                />
                {couponsDetails &&
                    <CouponDetails
                        couponsData={couponsDetails}
                    />
                }
                {couponsOrder && couponsOrder.results && Array.isArray(couponsOrder.results) && couponsOrder.results.length > 0 ?
                    <ListCouponOrderDeatailsTable
                        userDetails={userDetails}
                        coupon_uid={this.props.couponUid}
                        couponsData={couponsOrder}
                        getCouponOrderDetailsAction={this.props.getCouponOrderDetailsAction}
                    />
                    :
                    this.getNoOrders()
                }
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails,
        couponsOrder: state.couponAndOrdersDetails.couponsOrders,
        couponsDetails: state.couponAndOrdersDetails.couponsDetails
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getCouponDetailsAction,
        getCouponOrderDetailsAction,
        resetCouponData
    }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(ListCouponOrderDeatails);
