import React, { Component } from 'react';
import './style.scss';

class CouponDetails extends Component {

    render() {
        let { couponsData } = this.props;
        // console.log(couponsData);

        return <div className="coupon-details-conatiner">
            <div className="flex-container">
                <div className="status-label">Coupon UID </div><div>: {couponsData.coupon_uid || ""} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Type </div><div>: {couponsData.coupon_type_name || ""} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Classification </div><div>: {couponsData.coupon_classification_name || ""} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Code </div><div>: {couponsData.coupon_code || ""} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Percentage </div><div>: {couponsData.coupon_percentage} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Accessories Percentage </div><div>: {couponsData.coupon_accessories_percentage} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Cot Percentage </div><div>: {couponsData.coupon_cot_percentage} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Flat Discount </div><div>: {couponsData.coupon_flat_discount} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Max Discount </div><div>: {couponsData.coupon_max_discount}
                </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Cart Min Value </div><div>: {couponsData.coupon_min_amount} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Description </div><div>: {couponsData.coupon_description || ""} </div>
            </div>
            <div className="flex-container">
                <div className="status-label">Coupon Validity </div><div>: {couponsData.start_date} - {couponsData.end_date} </div>
            </div>
        </div>
    }
}

export default CouponDetails;