import React, { Component } from 'react';
import './style.scss';
import ReactTable from 'react-table';
import Timestamp, { timestampFilter } from '../../../Dashboard/components/Timestamp';
import PaginationComponent from '../../../../components/PaginationComponent';
const dropDownValues = [50, 100, 150, 200, 250, 300];
class ListCouponOrderDeatailsTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            offset: 0,
            limit: 100,
            total: 0,
        }
    }

    componentDidMount() {
        if (this.props.couponsData && this.props.couponsData.count && Number(this.props.couponsData.count) !== Number(this.state.total)) {
            this.setState({
                total: this.props.couponsData.count
            });
        }
    }

    componentWillReceiveProps(props) {
        if (props.couponsData && props.couponsData.count && Number(props.couponsData.count) !== Number(this.state.total)) {
            this.setState({
                total: props.couponsData.count
            });
        }
    }

    componentWillUnmount() {
        this.resetTable();
    }

    resetTable = () => {
        console.log("Reseting...");
    }

    onLimitChange = (event) => {
        this.setState({ limit: Number(event.target.value) }, () => this.handleClick("reset"));
    }

    handleClick = (type) => {
        let { limit, offset, total } = this.state;
        let { getCouponOrderDetailsAction, coupon_uid } = this.props;
        this.setState({
            offset: type === "next" ?
                offset + limit < total ? offset + limit : offset
                :
                type === "prev" && offset - limit >= 0 ? offset - limit : 0
        }, () => {
            let { limit, offset } = this.state;
            getCouponOrderDetailsAction && getCouponOrderDetailsAction({ coupon_uid, limit, offset });
        });
    }

    addressDetails = (row) => {
        let rowInfo = row.original;
        return (<div className="address break-word">
            <div className="break-word"><b>Address :</b> {rowInfo.address}</div>
            <div className="break-word"><b>Landmark :</b> {rowInfo.landmark}</div>
            <div className="break-word"><b>City :</b> {rowInfo.city}</div>
            <div className="break-word"><b>State :</b> {rowInfo.state_name} - {rowInfo.pincode}</div>
        </div>)
    }

    userDetails = (row) => {
        let rowInfo = row.original;
        return <div className="user-details break-word">
            <div className="break-word"><b>Name :</b> {rowInfo.firstname + ' ' + rowInfo.lastname}</div>
            <div className="break-word"><b>Email :</b> {rowInfo.email_address}</div>
            <div className="break-word"><b>Mobile Number :</b> {rowInfo.mobile_number}</div>
            {rowInfo.alternate_mobile_number && <div><b>[A] :</b> {rowInfo.alternate_mobile_number}</div>}
        </div>
    }

    orderDeatils = (row) => {
        let rowInfo = row.original;
        return (
            rowInfo && Array.isArray(rowInfo.items) && rowInfo.items.length > 0 &&
            rowInfo.items.map((items, index) => {
                return <div className="cart-details break-word">
                    <div><b>SKU :</b> {items.item_sku}</div>
                    {items.item_dimensions && <div><b>Dimension :</b> {items.item_dimensions}</div>}
                    <div><b>Qty :</b> {items.item_quantity}</div>
                    <div><b>Price :</b> INR {items.item_price} * {items.item_quantity} = {items.sub_total}</div>
                    {index < rowInfo.items.length - 1 && <hr />}
                </div >
            })
        )
    }

    totalPrice = (rowInfo) => {
        let order_complete_price = 0;
        if (rowInfo && Array.isArray(rowInfo.items) && rowInfo.items.length > 0) {
            rowInfo.items.map(items => order_complete_price += (Number(items.item_quantity || 0) * Number(items.item_price || 0)));
        }
        return order_complete_price;
    }

    totalDiscount = (rowInfo) => {
        let total_complete_price = 0, total_net_price = 0;
        if (rowInfo && Array.isArray(rowInfo.items) && rowInfo.items.length > 0) {
            rowInfo.items.map((items) => {
                total_complete_price += (Number(items.item_quantity || 0) * Number(items.item_price || 0));
                total_net_price += Number(items.net_price || 0);
                return "";
            });
        }
        return Number(total_complete_price) - Number(total_net_price);
    }

    cartPrice = (row) => {
        let rowInfo = row.original;
        return (
            <div className="cart-price break-word" >
                <div><b>Total :</b> ₹ {
                    rowInfo.total_price
                    // this.totalPrice(rowInfo)
                }</div>
                <hr />
                <div><b>Discount :</b> ₹ {
                    rowInfo.discount_price
                    // this.totalDiscount(rowInfo)
                }</div>
            </div >
        )
    }

    cartDetailsFilter = (filter, row) => {
        let data = row._original.items.some((items) => {
            return (items["item_sku"] && String(items["item_sku"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
                (items["item_dimensions"] && String(items["item_dimensions"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
                (items["item_quantity"] && String(items["item_quantity"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
                (items["item_price"] && String(items["item_price"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
                String(row[filter.id]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase());
        })
        return data;
    }

    userDetailsFilter = (filter, row) => {
        return (row._original["firstname"] && String(row._original["firstname"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["lastname"] && String(row._original["lastname"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["email_address"] && String(row._original["email_address"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["mobile_number"] && String(row._original["mobile_number"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["alternate_mobile_number"] && String(row._original["alternate_mobile_number"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()))
    }

    addressDetailsFilter = (filter, row) => {
        return (row._original["address"] && String(row._original["address"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["landmark"] && String(row._original["landmark"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["city"] && String(row._original["city"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["state_name"] && String(row._original["state_name"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            (row._original["pincode"] && String(row._original["pincode"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
            String(row[filter.id]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())
    }

    commentFilter = (filter, row) => {
        return (row._original["additional_comments"] && String(row._original["additional_comments"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()))
    }

    render() {
        console.log(this.props);
        let { couponsData } = this.props;
        return (
            <div className="coupons-table-container">
                <ReactTable
                    data={couponsData.results}
                    filterable
                    minRows={0}
                    pageSize={999}
                    PaginationComponent={() =>
                        <PaginationComponent
                            dropDownValues={dropDownValues}
                            onLimitChange={this.onLimitChange}
                            offset={this.state.offset}
                            total={this.state.total}
                            limit={this.state.limit}
                            onPrevClick={this.handleClick}
                            onNextClick={this.handleClick}
                        />
                    }
                    NoDataComponent={() => null}
                    defaultFilterMethod={(filter, row) =>
                        String(row[filter.id]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())}
                    columns={[{
                        Header: 'S.No',
                        id: 'row',
                        maxWidth: 50,
                        filterable: false,
                        Cell: (row) => <div>{row.index + 1}</div>
                    }, {
                        Header: "Order Id",
                        accessor: "order_random_id",
                        minWidth: 170,
                    }, {
                        Header: "Payment Mode",
                        accessor: "payment_type",
                        minWidth: 80
                    }, {
                        Header: "Order Cart",
                        accessor: "order_cart_details",
                        minWidth: 200,
                        Cell: this.orderDeatils,
                        filterMethod: this.cartDetailsFilter
                    }, {
                        Header: "Cart Price",
                        accessor: "cart_price_details",
                        minWidth: 180,
                        Cell: this.cartPrice,
                    }, {
                        Header: "User Details",
                        accessor: "user_details",
                        minWidth: 210,
                        Cell: this.userDetails,
                        filterMethod: this.userDetailsFilter
                    }, {
                        Header: "Address",
                        accessor: "address_details",
                        minWidth: 230,
                        Cell: this.addressDetails,
                        filterMethod: this.addressDetailsFilter
                    }, {
                        Header: "Additional Comments",
                        accessor: "additionalComments",
                        minWidth: 200,
                        filterMethod: this.commentFilter,
                        Cell: (row) => <div>
                            {row.original && <div className="break-word">
                                {row.original.additional_comments}</div>}
                        </div>
                    }, {
                        Header: "Create Timestamp",
                        accessor: "create_timestamp",
                        minWidth: 165,
                        Cell: Timestamp,
                        filterMethod: timestampFilter
                    }]}
                />
            </div>
        );
    }
}

export default ListCouponOrderDeatailsTable;
